<template>
  <div>
    <ul class="footer">
      <li><router-link :to="{path:'/Fromsub'}"><img src="../assets/img/tab_iocn_62.png" ><p>申请试听</p></router-link></li>
      <li @click="tel"><img src="../assets/img/tab_iocn_64.png" ><p>电话咨询</p></li>
      <li><img src="../assets/img/tab_iocn_66.png" ><p>在线咨询</p></li>
    </ul>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
 export default {
   data(){
     return{
				num:'0826-1234567'
     }
   },
   methods:{
     tel(){
       MessageBox({
         title: '',
         message: '<div style="font-size:33px;font-weight:bold;color: #000;height: 129px;line-height: 129px">'+this.num+'</div>',
         confirmButtonText:'立 即 拨 号'
       }).then(action => {
       	window.location.href='tel:'+this.num
       });
     }
   }
 }
</script>

<style>
.footer{
  display: flex;
  justify-content: space-around;
  height: 49px;
  border-bottom: 1px solid #ccc;
  align-items: center;
  position: fixed;
  bottom: 0px;
  width: 100%;
  background: #c01920;
  color: #ffffff;
  font-size: 10px;
}
.footer img{
  height: 20px;
  margin: 0 auto;
  margin-bottom: 6px;
}
.footer a{
  color: #fff;
  text-decoration: none;
}



  .mint-msgbox-btn{
    width:225px;
    height:50px;
    background: #c01920;
    position: absolute;
    left: 45px;
    bottom: 38px;
    border-radius: 10px;
  }
  .mint-msgbox-content{
  border-bottom: 0;
  }
  .mint-msgbox-confirm{
    color: #fff;
  }
</style>



