import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home'
import Job from '../pages/job'
import Kec from '../pages/kec'
import Teacher from '../pages/teacher'
import Environmental from '../pages/environmental'
import Our from '../pages/our'
import Fromsub from '../pages/From'
import News from '../pages/News'
import News1 from '../pages/News1'
import Course from '../pages/Course'
import Message from '../pages/Message'
Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/Home',
      component: Home
    },
    {
      path: '/Job',
      component: Job
    },
    {
      path: '/Kec',
      component: Kec
    },
    {
      path: '/Teacher',
      component: Teacher
    },
    {
      path: '/Environmental',
      component: Environmental
    },
    {
      path: '/Our',
      component: Our
    },
    {
      path: '/Fromsub',
      component: Fromsub
    },
    {
      path: '/News',
      name: 'News',
      component: News
    },
    {
      path: '/News1',
      name: 'News1',
      component: News1
    },
    {
      path: '/Course',
      name: 'Course',
      component: Course
    },
    {
      path: '/Message',
      name: 'Message',
      component: Message
    }
  ]
})



